.banner {
  padding: 3rem 5rem 3rem 5rem;
  display: flex;
  align-items: center;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 10%;
}

.banner-section {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.banner-logo {
  min-width: 100px;
  min-height: 100px;
  margin-right: 20px;
  background-image: url("../../assets/white-logo.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.banner-header {
  min-width: 200px;
}

.banner-header h1 {
  font-size: 4rem;
  font-weight: lighter;
}

.banner-header h3 {
  color: #ff4c8b;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  text-align: left;
  margin-bottom: 5px;
}

.banner-title {
  display: flex;
  align-items: flex-end;
}

.banner-title h1 {
  margin-top: -20px;
  margin-bottom: 0px;
}

.tag {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 35px;
  border-radius: 10px;
  background-color: #ff4c8b;
  color: white;
  text-transform: uppercase;
  font-weight: bold;
  margin: 0px 0px 20px 15px;
}

@media only screen and (max-width: 750px) {
  .banner {
    padding-right: 0px;
  }
  .banner-header h1 {
    font-size: 50px;
  }
  .banner-header h3 {
    font-size: 20px;
  }
  .banner-title {
    flex-direction: column;
    align-items: start;
  }
  .tag {
    margin: 0px;
  }
}
